.thread {
    position: relative;
    border-bottom: 1px solid #d2d2d2;
    padding: 10px;
    margin-bottom: 15px;
    margin-left: 10px;
}

.thread__title {
    text-align: end;
}

.thread__text {
    margin: 16px 36px;
    font-size: 15px;
    line-height: 1.3;
    color: #333;
}

.thread_info {
    position: absolute;
    top: 10px;
    left: 20px;
    font-size: 0.9em;
    color: #5d5d5d;
}

.thread__comment {
    position: relative;
}
